                                        <rect width="297" height="265" fill="url(#pattern0)"/>
                                        <defs>
                                            <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1"
                                                     height="1">
                                                <use xlinkHref="#image0_3_236"
                                                     transform="matrix(0.000873049 0 0 0.000978474 -0.000256973 0)"/>
                                            </pattern>
