import {useState} from "react";
import InputMask from "react-input-mask";
import {tg} from "../index";

export const Selected = () => {
    const [step, setStep] = useState(1);
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');

    const sendToTg = (e) => {
        e.preventDefault();
        const chatId = tg.initDataUnsafe?.query_id;

        fetch(`https://handsome-pear-handkerchief.cyclic.app?name=${name}&phone=${phone}&chatId=${chatId}`, {
            method: 'GET'
        })
    }

    const changeStep = (n) => {
        setStep(n)
    }
    return (
        <section className="selected-apartment selected-apartment-main container">

            <form>
                {step == 1 && <div className="selected-apartment__step selected-apartment__step-1 is-active" data-step-next="2">
                    <h2 className="h1">Срок сдачи?</h2>
                    <div className="step-variants step-1__variants">
                        <div onClick={() => changeStep(2)} className="variant step-1__variant">На старте продаж</div>
                        <div onClick={() => changeStep(2)} className="variant step-1__variant">Дом сдан</div>
                        <div onClick={() => changeStep(2)} className="variant step-1__variant">сдача 2023</div>
                        <div onClick={() => changeStep(2)} className="variant step-1__variant">2024 и позже</div>

                        <a href="/#/" className="button">Назад</a>
                    </div>
                    <div className="progress">
                        <div className="progress-title">25%</div>
                        <div className="progress-item">
                            <div style={{width: "25%"}} className="progress-value"></div>
                        </div>
                    </div>
                </div>}
                {step == 2 && <div className="selected-apartment__step selected-apartment__step-2 is-active" data-step="2" data-step-next="3">
                    <h2 className="h1">Выберете количество комнат</h2>
                    <div className="step-variants step-1__variants">
                        <div onClick={() => changeStep(3)} className="variant step-1__variant">Студия</div>
                        <div onClick={() => changeStep(3)} className="variant step-1__variant">Однокомнатная</div>
                        <div onClick={() => changeStep(3)} className="variant step-1__variant">Двухкомнатная</div>
                        <div onClick={() => changeStep(3)} className="variant step-1__variant">Трехкомнатная</div>
                        <a onClick={(e) => {
                            e.preventDefault()
                            setStep(step-1)
                        }
                        } className="button">Назад</a>
                    </div>
                    <div className="progress">
                        <div className="progress-title">50%</div>
                        <div className="progress-item">
                            <div style={{width: "50%"}} className="progress-value"></div>
                        </div>
                    </div>
                </div>}
                {step == 3 && <div className="selected-apartment__step selected-apartment__step-3 is-active" data-step="3" data-step-next="4">
                    <h2 className="h1">Комфортный ежемесячный платеж</h2>
                    <div className="step-variants step-2__variants">
                        <div onClick={() => changeStep(4)} className="variant step-2__variant">до 15 000 ₽</div>
                        <div onClick={() => changeStep(4)} className="variant step-2__variant">до 20 000 ₽</div>
                        <div onClick={() => changeStep(4)} className="variant step-2__variant">до 20 000 ₽</div>
                        <div onClick={() => changeStep(4)} className="variant step-2__variant">Не имеет значения</div>
                        <a onClick={(e) => {
                            e.preventDefault()
                            setStep(step-1)
                        }
                        } className="button">Назад</a>
                    </div>
                    <div className="progress">
                        <div className="progress-title">75%</div>
                        <div className="progress-item">
                            <div style={{width: "75%"}} className="progress-value"></div>
                        </div>
                    </div>
                </div>
                }
                {step == 4 && <div className="selected-apartment__step selected-apartment__step-4 is-active" data-step="4">
                    <h2 className="h1">Оставьте заявку</h2>
                    <div className="selected-apartment-main__submit" style={{justifyContent: 'start'}}>
                        <div className="selected-apartment-main__submit-items">
                            <input value={name} onChange={(e) => setName(e.target.value)} id="name" className="form-input" type="text" placeholder="Ваше имя" />
                            <InputMask
                                value={phone}
                                onChange={(e) => setPhone(e.target.value)}
                                className="form-input"
                                type="tel"
                                placeholder="+7 (___) ___-__-__"
                                mask="+7 (999) 999-99-99"
                            />
                                    <p className="form-politic">Нажимая кнопку «Отправить», вы подтверждаете
                                        свое согласие на обработку <span>персональных данных</span></p>
                        </div>
                        <button style={{marginTop: '8px'}} onClick={(e) => sendToTg(e)} className="form-button button button-color" >Отправить</button>
                        <a style={{marginTop: '8px'}} href="/#/" className="button">Назад</a>
                    </div>
                </div>}
            </form>
            <div className="pattern"></div>
        </section>
    )
}